/* Card Header */
 
.style_CardHeader__3qrPG{
    display: flex;
    justify-content: space-between;
}

.style_switch__OEjMU {
    margin-right: 10px;
}
.style_switch__OEjMU input {
    display: none;
}
.style_switch__toggle__3YrTb{
    width: 6rem;
}
.style_switch__toggle__3YrTb label{
    display: flex;
    align-items: center;
    width: 100%;
    height: 3rem;
    box-shadow: 0.3rem 0.3rem 0.6rem var(--greyLight-2), -0.2rem -0.2rem 0.5rem var(--white);
    background: rgba(255, 255, 255, 0);
    position: relative;
    cursor: pointer;
    border-radius: 1.6rem;
}
.style_switch__toggle__3YrTb label::after {
    content: "";
    position: absolute;
    left: .4rem;
    width: 2.1rem;
    height: 2.1rem;
    border-radius: 50%;
    background: var(--greyDark);
    transition: all .4s ease;
}
.style_switch__toggle__3YrTb label::before {
    content: '';
    width: 100%;
    height: 100%;
    border-radius: inherit;
    background: linear-gradient(330deg, var(--primary-dark) 0%, var(--primary) 50%, var(--primary-light) 100%);
    opacity: 0;
    transition: all .4s ease;
}
.style_switch__OEjMU input:checked ~ label::before {
    opacity: 1;
}
.style_switch__OEjMU input:checked ~ label::after {
    left: 57%;
    background: var(--greyLight-1);
} 

